const postData = async (url, data) => {  //    form
    const res = await fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: data
    });
    return await res.json();
};


const getItems = async (url) => {  //    cards
    const res = await fetch(url);
    //   ,    fetch     catch,     
    if (!res.ok) { //   fetch 
        throw new Error(`Could not fetch ${url}, status: ${res.status}`);
    }
    return await res.json();
};

export { postData };
export { getItems };

